import http from '../../utils/http.js';
export default {
	name: 'loginDuanxin',
	data() {
		return {
			phone: "",
			regist: {
				url: 'resetPassword',
				method: 'post'
			},
			verification: {
				url: 'sendVerifyCode',
				method: 'post'
			},
			verificationData: {
				"phone": this.phone,
				"type": "2"
			},
			verificationTxt: '获取验证码'
		}
	},
	methods: {
		// 获取验证码
		getYanZhengMa: function() {
			this.verificationData.phone = this.phone
			console.log(this.verificationData)
			http.httpRequest(this.verification, this.verificationData).then(res => {
				console.log(res);
				if (res.data.code == 200) {
					this.verificationTxt = 60
					var timeId = setInterval(() => {
						this.verificationTxt--
						if (this.verificationTxt == 0) {
							this.verificationTxt = '重新发送'
							clearInterval(timeId)
						}
					}, 1000)
				}
			}, error => {
				console.log(error);
			})
		},
		formSubmit: function(e) {
			console.log(e.detail.value)
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			http.httpRequest(this.regist, JSON.stringify(e.detail.value)).then(res => {
				console.log(res);
				if (res.data.code == 500) {
					uni.showToast({
						title: res.data.msg,
						icon: 'none',
						duration: 1000
					})
				}
				if (res.data.code == 200) {
					uni.showToast({
						title: '修改成功！',
						icon: 'success',
						duration: 2000,
						success: function() {
							console.log('haha');
							setTimeout(function() {
								//要延时执行的代码
								uni.navigateTo({
									url: '../login/zhanghao'
								})
							}, 2000) //延迟时间
						}
					})
				}
			}, error => {
				console.log(error);
			})
		}
	},
}
